<template>
  <div class="box">
    <my-header></my-header>
    <div class="searchBox">
      <!--搜索框-->
      <div class="searchInputBox">
        <div>
          <img src="../assets/search.png" class="searchLogo">
          <input class="search-input" @keydown="enterKeyDown" @input="inputing" v-model="inputValue" type="text" @focus="inputFocus" placeholder="请输入歌曲名称">
        </div>
        <span class="searchBtn" @click="seatchBtnClick" v-if="isShowBtn">搜索</span>
      </div>
      <!--热门搜索-->
      <section class="hotSearch" v-if="!isShowBtn">
        <section>热门搜索</section>
        <div @click="hotKeyClick(hotkey)" class="hotkey" :class="{'specialKey' : index==0 }" v-for="(hotkey,index) in formatKeywordList">
          {{hotkey.k}}
        </div>
      </section>
      <!--搜索结果列表-->
      <div v-for="(item,index) in searchList" class="searchListItem" @click="searchListItemClick(item)" v-if="isShowResult">
        <div class="searchListItemLogo"></div>
        <div>
          <h3>{{item.songname}}</h3>
          <p>
            <span v-for="singer in item.singer">{{singer.name}} </span>
          </p>
        </div>
      </div>
      <!--历史搜索-->
      <ul v-if="isShowHistory">
        <li v-for="(item,index) in storageDataArr" @click="historyWordClick(item)">
          <div></div>
          <span>{{item}}</span>
        </li>
        <li v-if="storageDataArr.length > 0" @click="clearHistory" class="clearHistoryBtn">清除记录</li>
      </ul>
    </div>
  </div>
</template>
<script>
import MyHeader from '../components/MyHeader'
import router from '../router'
import store from '../vuex/store'
export default {
  data() {
    return {
      hotKeyWordList: [],      //热搜关键字list
      isShowBtn: false,          //控制热门搜索区域和搜索按钮的展示
      searchList: [],
      isShowResult: false,
      inputValue: '',
      storageDataArr: [],
      isShowHistory:false
    }
  },
  computed: {
    formatKeywordList: function () {
      return this.hotKeyWordList.slice(0, 15)
    }
  },
  methods: {
    inputFocus: function () {
      this.isShowBtn = true
      this.isShowHistory = true
    },
    seatchBtnClick: function () {
      if (this.inputValue == '') return
      this.search()
    },
    search: function () {
      this.isShowResult = true
      this.isShowHistory = false
      this.saveData()
      this.$http.jsonp('https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp', {
        params: {
          w: this.inputValue
        },
        jsonp: 'jsonpCallback'
      }).then(function (response) {
        this.searchList = response.body.data.song.list
      })
    },
    inputing: function () {
      this.isShowResult = false
    },
    searchListItemClick: function (item) {
      store.commit('changeCurrentSong', item)
      router.push({
        name: 'playing',
        params: {
          songid: item.songid
        }
      })
    },
    enterKeyDown: function (ev) {
      if (ev.key == 'Enter') {
        this.search()
      }
    },
    hotKeyClick: function (hotkey) {
      this.isShowBtn = true
      this.inputValue = hotkey.k
      this.search()
    },
    saveData: function () {
      this.storageDataArr = this.getData()
      for(var i=0;i<this.storageDataArr.length;i++) if(this.storageDataArr[i]==this.inputValue) return
      this.storageDataArr.push(this.inputValue)
      var str = JSON.stringify(this.storageDataArr)
      localStorage.setItem('storageDataArr', str)
    },
    getData: function () {
      var str = localStorage.getItem('storageDataArr')
      if(!str){
        return new Array()
      }
      return JSON.parse(str)
    },
    clearHistory:function(){
      localStorage.setItem('storageDataArr', '')
      this.storageDataArr = ''
    },
    historyWordClick:function(value){
      this.inputValue = value
      this.search()
      this.isShowHistory = false
    }
  },
  components: {
    MyHeader
  },
  beforeMount() {
    this.$http.jsonp('https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg', {
      jsonp: 'jsonpCallback',
    }).then(function (response) {
      this.hotKeyWordList = response.body.data.hotkey
      // 强烈推荐的热搜放在首位
      this.hotKeyWordList.shift()
      for (var i = 0; i < this.hotKeyWordList.length; i++) if (this.hotKeyWordList[i].k.trim() == response.body.data.special_key) this.hotKeyWordList.splice(i, 1)
      this.hotKeyWordList.unshift({ k: response.body.data.special_key, n: response.body.data.special_url })
    })
    this.storageDataArr = this.getData()
  }
}
</script>

<style scoped>
.box {
  background-color: #eee;
}

.searchBox {
  font-size: .16rem;
  padding-bottom: .03rem;
}

.searchInputBox {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: .1rem;
}

.searchLogo {
  width: .32rem;
  height: .32rem;
}

.searchInputBox>div {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: .1rem 0;
}

.searchInputBox>div>input {
  border: none;
  flex: 1;
  height: 0.3rem;
  margin-left: .1rem;
}

.searchInputBox .searchBtn {
  padding-left: 0.1rem;
}

.hotSearch {
  background-color: #fff;
  padding: .1rem;
  overflow: hidden;
}

.hotSearch>section {
  color: gray;
}

.specialKey {
  color: red !important;
  border: 1px solid red !important;
}

.hotkey {

  border: 1px solid gray;
  border-radius: .4rem;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  float: left;
  margin-left: 0.3rem;
  padding: 0 .2rem;
  margin-top: 0.1rem;
}

.hotkey a {
  color: gray;
}

.search-input {
  outline: none;
}

.searchListItem {
  display: flex;
  border-bottom: 1px solid #eee;
  line-height: .35rem;
  background-color: #fff;
  align-items: center;
  height: 1rem;
}

.searchListItemLogo {
  width: .5rem;
  height: .5rem;
  background-image: url(../assets/search_sprite.png);
  background-repeat: no-repeat;
  background-size: .5rem .6rem;
  background-position: 0 0.1rem;
  margin: .1rem;
  margin-right: .3rem;
}

.searchListItem p {
  color: gray;
  width: 5rem;
  overflow: hidden;
  text-overflow: ellipsis;
}

.searchListItem h3 {
  font-size: .3rem;
  font-weight: normal;
}
ul{
  background-color: #fff;
}
ul li{
  border-bottom: 1px solid #eee;
  text-align: center;
  color: gray;
  height: 1rem;
  display: flex;
  align-items: center;
  font-size: 0.25rem;
}
ul li div{
  width: 0.3rem;
  height: 0.4rem;
  background-image: url(../assets/search_sprite.png);
  background-repeat: no-repeat;
  background-size: 0.7rem 0.85rem;
  background-position: 0 -0.55rem;
  margin: .1rem;
}
.clearHistoryBtn{
  background-color: rgba(222, 0, 0, 0.7);
  color: #fff;
  line-height: 1rem;
}
</style>




