<template>
    <div>
        <audio :src="songUrl" autoplay loop @timeupdate="timeupdate"></audio>
        <img :src="pic" class="playing-bgImg">
        <div class="blurDiv"></div>
        <div class="playing-header">
            <img class="header-img" :class="{'header-img-circle' : isPlaying}" :src="pic">
            <div class="header-detail">
                <p>{{song.songname}}</p>
                <span v-for="singer in song.singer">{{singer.name}} </span>
            </div>
            <div class="playingIcon" @click="playOrPause" :class="{'playIcon-playing' :isPlaying }"></div>
        </div>
        <div class="lyricBox">
            <p v-for="lyric in lyricList" v-html="lyric.l"></p>
        </div>
        <div class="footer">
            <span>{{curTime | formatTime}}</span>
            <div>
                <i></i>
                <span></span>
            </div>
            <span>{{song.interval | formatTime}}</span>
        </div>
    </div>
</template>
<script>
import store from '../vuex/store'
import Base64 from '../base64'
import $ from 'jquery'

export default {
    data() {
        return {
            song: store.getters.getCurrentSong,
            songid: this.$route.params.songid,      //歌曲id
            songUrl: '',
            pic: '',
            interval: '',
            isPlaying: true,
            audio: '',
            curTime: 0,
            isPlaying: true,
            lyricList: []
        }
    },
    beforeMount() {
        this.$http.jsonp('https://api.darlin.me/music/lyric/' + this.songid, {
            jsonp: 'callback'
        }).then(function (response) {
            var str = Base64.decode(response.body.lyric)
            var count = 0;
            for (var i = 0; i < str.length; i++) {
                if (str[i] == ']') {
                    count++
                    if (count == 5) {
                        str = str.slice(i + 1)
                    }
                }
            }
            var arr = str.split('\n')
            arr.shift()
            for (var i = 0; i < arr.length; i++) {
                var sArr = arr[i].slice(1).split(']')
                var second = parseInt(sArr[0].substr(0, 2)) * 60 + parseFloat(sArr[0].substr(3))
                if (sArr[1] == '') sArr[1] += '&nbsp;'
                this.lyricList.push({ t: second, l: sArr[1] })
            }
        });
    },
    mounted() {
        this.songUrl = 'http://ws.stream.qqmusic.qq.com/' + this.songid + '.m4a?fromtag=46'
        this.pic = 'http://y.gtimg.cn/music/photo_new/T002R300x300M000' + this.song.albummid + '.jpg'
        this.audio = document.querySelector('audio')
        this.progressValue = document.querySelector('.footer>div>span')
        this.progressDot = document.querySelector('.footer>div>i')
    },
    methods: {
        playOrPause: function () {
            this.isPlaying = !this.isPlaying
            if (this.isPlaying) {
                this.audio.play()
            } else {
                this.audio.pause()
            }
        },
        timeupdate: function () {
            this.curTime = Math.floor(this.audio.currentTime)
            this.progressValue.style.width = this.curTime / this.audio.duration * 4 + 'rem'
            this.progressDot.style.left = this.curTime / this.audio.duration * 4 - 0.15 + 'rem'
            if (this.curTime >= this.song.interval) {
                this.curTime = 0
            }
            // 更新歌词
            var _this = this
            $(document).ready(function () {
                for (var i = 0; i < _this.lyricList.length; i++) {
                    var obj = _this.lyricList[i]
                    if (_this.audio.currentTime >= obj.t) {
                        $('.lyricBox p')[i].className = 'currentLyric'
                        var dom = $('.lyricBox p')[i]
                        $(dom).siblings().removeClass('currentLyric')
                    } else {
                        $('.lyricBox p')[i].className = ''
                    }
                }
            })
        }
    },
    filters: {
        formatTime: function (value) {
            var minute, second
            minute = (parseInt(value / 60) < 10) ? '0' + Math.floor(parseInt(value / 60)) : Math.floor(parseInt(value / 60))
            second = (parseInt(value % 60) < 10) ? '0' + Math.floor(parseInt(value % 60)) : Math.floor(parseInt(value % 60))
            return minute + ':' + second
        }
    }
}
</script>

<style scoped>
audio {
    display: none;
}

body {
    overflow: hidden;
    font-size: .16rem;
    height: 100%;
}

.blurDiv {
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 12rem;
    background-color: #000;
    opacity: 0.7;
}

.playing-bgImg {
    z-index: -2;
    position: absolute;
    left: 0;
    top: 0;
    width: 7.2rem;
    height: 12rem;
    filter: blur(.1rem);
}

.playing-header {
    background-color: rgba(0, 0, 0, 0.2) !important;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .2rem .3rem;
    font-size: .3rem;
    height: 88px;
}

.header-img {
    border-radius: 50%;
    width: 66px;
    height: 66px;
}

.header-img-circle {
    animation: circle linear 30s infinite;
}

.playing-header .header-detail {
    flex: 1;
    padding-left: .2rem;
}

.playing-header .header-detail span {
    font-size: .16rem;
}

.playingIcon {
    display: block;
    width: 0.6rem !important;
    height: 0.6rem;
    background-image: url(../assets/list_sprite.png);
    background-size: 0.6rem 1.5rem;
    background-position: 0 0;
    background-repeat: no-repeat;
}

.playIcon-playing {
    background-position: 0 -0.63rem !important;
}

@keyframes circle {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

.footer {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
    height: 100px;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer>span {
    color: gray;
    font-size: 20px;
}

.footer>div {
    position: relative;
    width: 4rem;
    height: .05rem;
    border-radius: .1rem;
    background-color: gray;
    margin: 0 .3rem;
}

.footer>div>i {
    position: relative;
    top: -0.13rem;
    left: -0.15rem;
    display: block;
    width: .3rem;
    height: .3rem;
    border-radius: 50%;
    border: .1rem dashed rgba(114, 194, 125, 0.5);
    animation: flash linear 2s infinite;
    animation-name: flash, circle;
}

.footer>div>span {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0rem;
    height: .05rem;
    border-radius: .1rem;
    background-color: rgb(114, 194, 125);
}

@keyframes flash {
    0%,
    100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

.lyricBox {
    position: relative;
    margin-top: 50px;
    color: gray;
    text-align: center;
    height: 300px;
    overflow-y: scroll;
    font-size: 15px;
}

.currentLyric {
    color: rgb(114, 194, 125);
    font-size: 20px;
}
</style>




