<template>
    <div class="body">
        <img :src="topinfo.pic_album" class="bgImg">
        <section>
            <div>{{topinfo.ListName}}</div>
            <span>{{date}} 更新</span>
            <i class="playIcon"></i>
        </section>
        <div v-for="(item,index) in songList" class="songItem" @click="goPlaying(item)">
            <div class="songIndex">{{index+1}}</div>
            <div>
                <h3>{{item.data.songname}}</h3>
                <p>
                    <span v-for="singer in item.data.singer">{{singer.name}} 👉 </span>{{item.data.albumname}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import router from '../router'
import store from '../vuex/store'
export default {
    data() {
        return {
            id: this.$route.params.id,
            topinfo: '',
            songList: '',
            date: ''
        }
    },
    methods: {
        goPlaying: function (item) {
            store.commit('changeCurrentSong',item.data)
            router.push({
                name: 'playing',
                params: {
                    songid: item.data.songid
                }
            });
        }
    },
    beforeMount() {
        this.$http.jsonp('https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg', {
            params: {
                topid: this.id
            },
            jsonp: 'jsonpCallback'
        }).then(function (response) {
            this.topinfo = response.body.topinfo
            this.songList = response.body.songlist
            this.date = response.body.date
            console.log(response.body)
        })
    }
}
</script>

<style scoped>
.body {
    font-size: .16rem;
    background-color: #000;
    color: #fff;
}
.body a {
    text-decoration: none;
    color: #fff !important;
}
.bgImg {
    width: 100%;
}
section {
    position: relative;
    height: 4rem;
    background-image: -webkit-linear-gradient(top, transparent, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 1));
    padding-top: 2.7rem;
    margin-top: -4rem;
    color: #fff;
    font-weight: normal;
    font-size: .3rem;
    padding-left: .1rem;
    padding-right: .1rem;
}

section span {
    font-size: .16rem;
}

.playIcon {
    display: block;
    width: 0.6rem;
    height: 0.6rem;
    background-image: url(../assets/list_sprite.png);
    background-size: 0.6rem 1.5rem;
    background-position: 0 0;
    position: absolute;
    right: .1rem;
    bottom: .65rem;
}

.songItem {
    display: flex;
    border-bottom: 1px solid #111;
    background-color: black;
    line-height: .35rem;
}

.songIndex {
    width: .7rem;
    height: .7rem;
    line-height: .7rem;
    text-align: center;
    font-size: .2rem;
}

.songItem p {
    color: gray;
    width: 5rem;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>




