<template>
    <div>
        <my-header>
    
        </my-header>
        <div class="banner">
            <swiper :options="swiperOption" ref="mySwiper">
                <swiper-slide v-for="item in slideList">
                    <img :src="item.picUrl" />
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
            <div class="radio">
                <div class="">电台</div>
                <div class="radioContainer">
                    <div v-for="item in radioList" class="radioItem">
                        <img :src="item.picUrl" />
                        <p>{{item.Ftitle}}</p>
                        <i class="playIcon"></i>
                    </div>
                </div>
            </div>
            <footer>
                <div class="hot">热门歌单</div>
                <div>
                    <a href="http://baidu.com">去客户端发现更多好音乐go
                        <br>访问QQ音乐网页版</a>
                    <br>
                    <img src="../assets/logo_footer.png" class="logo_footer" />
                    <br>
                    <span class="copyright">Copyright © 1998 - 2017 Tencent. All Rights Reserved.</span>
                </div>
            </footer>
        </div>
    </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import MyHeader from '../components/MyHeader'
export default {
    data() {
        return {
            slideList: [],           // 轮播数组
            radioList: [],           // 电台数组
            currentTab: '',           // 当前选中的tab
            swiperOption: {
                notNextTick: true,
                pagination: '.swiper-pagination',
                slidesPerView: 'auto',
                centeredSlides: true,
                paginationClickable: true,
                autoplayDisableOnInteraction: false,
                spaceBetween: 30,
                autoplay: 2000,
                mousewheelControl: false
            }
        }
    },
    components: {
        swiper,
        swiperSlide,
        MyHeader
    },
    beforeMount() {
        this.$http.jsonp('https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg', {
            jsonp: 'jsonpCallback'
        }).then(function (response) {
            console.log(response.body.data)
            this.slideList = response.body.data.slider
            this.radioList = response.body.data.radioList
        })
    },

}
</script>
<style scoped>
body {
    background-color: #eee;
}

.banner img {
    width: 100%;
}

.swiper-pagination {
    position: absolute;
    bottom: .2rempx !important;
}

.radio {
    font-size: .18rem;
    padding: 0 .1rem;
}

.radioItem {
    position: relative;
}

.radioContainer {
    display: flex;
    justify-content: space-between;
}

.radioContainer img {
    width: 3.45rem;
}

.playIcon {
    display: block;
    width: 0.48rem;
    height: 0.48rem;
    background-image: url(../assets/list_sprite.png);
    background-size: 0.48rem 1.2rem;
    background-position: 0 0;
    position: absolute;
    right: .1rem;
    bottom: .4rem;
}

footer {
    margin-top: .1rem;
    background-color: #fff;
    font-size: .18rem;
    padding: .1rem .1rem;
    text-align: center;
    line-height: 250%;
}

footer .hot {
    text-align: left;
}

footer a {
    text-decoration: none;
    color: gray;
}

footer .logo_footer {
    width: 1.2rem;
    margin: .1rem 0;
}

footer .copyright {
    color: #a6a6a6;
}
</style>





