<template>
    <div class="header">
        <div @click="changeTab(0)" class="currentTab"><router-link to="/" >推荐</router-link></div>
        <div @click="changeTab(1)"><router-link to="/chart" >排行榜</router-link></div>
        <div @click="changeTab(2)"><router-link to="/search">搜索</router-link></div>
    </div>
</template>
<script>
export default {
  data(){
      return{

      }
  },
  methods:{
    changeTab:function(index){
        var divs = document.querySelectorAll('.header div')
        for(var i=0; i<divs.length;i++){
            i == index ? divs[i].className = 'currentTab' : divs[i].className=''
        }
    }
  }
}
</script>

<style scoped>
    .header{
        overflow: hidden;
        background-color: #fff;
    }
    .header div{
        width: 33.33333%;
        float: left;
    }
    .header div a{
        display: block;
        width: 100%;
        height: .6rem;
        font-size: 0.3rem;
        color: gray;
        text-decoration: none;
        font-weight: normal;
        text-align: center;
        line-height:  .6rem;
    }
    .currentTab{
        color: lightgreen ;
        border-bottom: .03rem solid lightgreen;
    }
    
</style>




