import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        currentSong:''
    },
    mutations:{
        changeCurrentSong(state,song){
            state.currentSong = song
        }
    },
    getters:{
        getCurrentSong(state,getters){
            return state.currentSong
        }
    }
})




