<template>
    <div class="box">
        <my-header></my-header>
        <div class="chartBox">
            <router-link :to="{name:'chartDetail',params:{id:chart.id}}" class="chart-item" v-for="chart in chartList">
                <i>{{chart.listenCount | formatListenCount}}</i>
                <img :src="chart.picUrl">
                <div class="chart-detail">
                    <div class="chart-detail-topTitle">{{chart.topTitle}}</div>
                    <div class="chart-detail-songlist" v-for="(song,index) in chart.songList">{{index+1+'. ' + song.songname + ' - '}}
                        <span>{{song.singername}}</span>
                    </div>
                </div>
                <div class="chart-arrow">》</div>
            </router-link>
        </div>
    </div>
</template>
<script>
import MyHeader from '../components/MyHeader'
export default {
    data() {
        return {
            chartList: []
        }
    },
    beforeMount() {
        this.$http.jsonp('https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg', {
            jsonp: 'jsonpCallback'
        }).then(function (response) {
            this.chartList = response.body.data.topList
        })
    },
    components:{
        MyHeader
    }
}
</script>
<style scoped>
.box{
    background-color: #eee;
}

.chartBox {
    padding: 0 0.1rem;
    font-size: .16rem;
    margin: .1rem 0;
}

.chart-item {
    position: relative;
    background-color: #fff;
    display: flex;
    margin: .1rem 0;
}

.chart-item>img {
    width: 1.4rem;
    height: 1.4rem;
}

.chart-item i {
    position: absolute;
    color: lightgray;
    left: .1rem;
    bottom: .05rem;
    font-style: normal;
    font-size: .12rem;
}

.chart-detail {
    padding-left: .1rem;
    flex: 1;
}

.chart-detail-topTitle {
    font-weight: bold;
    font-size: .2rem;
    margin-bottom: .15rem;
}

.chart-detail-songlist {
    font-size: .14rem;
}

.chart-detail-songlist span {
    color: gray;
}

a {
    text-decoration: none;
    color: #111;
}

.chart-arrow {
    position: absolute;
    right: 0;
    height: 1.2rem;
    line-height: 1.2rem;
    color: lightgray;
}
</style>





