import Vue from 'vue'
import Router from 'vue-router'
import index from '@/view/index'
import chart from '@/view/chart'
import search from '@/view/search'
import chartDetail from '@/view/chartDetail'
import playing from '@/view/playing'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'index',
            component: index
        },
        {
            path: '/chart',
            name: 'chart',
            component: chart
        },
        {
            path: '/search',
            name: 'search',
            component: search
        },
        {
            path: '/chartDetail/:id',
            name: 'chartDetail',
            component: chartDetail
        },
        {
            path: '/playing/:songid',
            name: 'playing',
            component: playing
        }
    ]
})


